import {defineStore} from "pinia";
export const useGlobalStore = defineStore('globalStore', {
    state: () => {
        return {
            username: 'alice',
            sampleListScale:5
        }
    },
    getters: {
        getName: (state) => `姓名：${state.username}`
    },
    actions: {
        changeName() {
            this.username = this.username + '~'
        }
    }
})